import getShops from '../../pagesLogin/components/getShopList.js'
import {
    getJurisdiction
} from '../../components/my-components/getJurisdiction.js'
let cacheData = {}; // 
function checkJurisdiction() { //
    let that = this;
    return new Promise((resolve, reject) => {
        getJurisdiction.call(that, true).then(res => {
            resolve(res);
        }).catch(res => {
            that.closePageLoading();
            reject(res);
        })
    })

}

function selectShop() { //
    let that = this;

    return new Promise((resolve, reject) => {
        that.Request('shoplist', { //
            pageSize: 2,
            page: 1,
            source: 'assistant'
        }).then(res => {

            if (res.error == 0) {

                let shops = getShops(res.list);

                if (shops.length == 1) { //
                    let shop = shops[0];
                    that.Cacher.setData('selectShop', {
                        from: 'selectShop',
                        shopInfo: shop.shopInfo,
                        totalShops: res.total,
                        left: shop.days > 0 ? (shop.days + '') : '',
                        expireDay: shop.days,
                    });
                    that.Request('switchShop', {
                        id: shop.shopInfo.id
                    }).then(res => {
                        if (res.error == 0) {
                            checkJurisdiction.call(that).then(res => { //
                                if (res['apps_index_view']) {
                                    uni.reLaunch({
                                        url: '../index/index?from=selectShop&status=onlyOne'
                                    });
                                } else {
                                    resolve()
                                }
                            }).catch(res => {
                                resolve()
                            })
                        } else {
                            that.Toast(res.message || '')
                        }
                    }).catch(res => {

                        that.Toast(res.message)
                    })
                } else {
                    resolve()
                }
            } else {
                resolve()
            }
        }).catch(res => {
            resolve()
        })


    })
}

function getWxInfo() { //
    let that = this;
    return new Promise((resolve, reject) => {
        uni.login({ //
            provider: 'weixin',
            success: function (loginRes) {
                that.Request('wechatLogin', { //session
                    code: loginRes.code
                }).then(res => {
                    that.Cacher.setData('needBindWx', {
                        testWx: true, //
                        needBind: true, //true
                        haveBind: false, //
                    });
                    if (res.error == 0) {
                        cacheData = Object.assign(cacheData, res);

                        that.Cacher.setData('login', cacheData);
                    }
                    uni.getUserInfo({ // 
                        provider: 'weixin',
                        success: function (infoRes) {
                            cacheData = Object.assign(cacheData, infoRes);
                            that.Cacher.setData('login', cacheData);
                            resolve(res)
                        },
                        fail(res) {
                            console.log('get info fails', res)
                            resolve(res)
                        }
                    });

                }).catch(res => {
                    that.Cacher.setData('needBindWx', {
                        testWx: true, //
                        needBind: true, //true
                        haveBind: false, //
                    })
                    resolve(res)
                })
            }
        });
    })
}

export function wxLogin() { //
    let that = this;
    return new Promise((resolve, reject) => {
        uni.login({ //
            provider: 'weixin',
            success: function (loginRes) {
                that.Request('wechatLogin', { //session
                    code: loginRes.code
                }).then(res => {
                    if (res.error == 0) {
                        cacheData = res;
                        that.Cacher.setData('login', cacheData);
                        that.Request('login', {
                            account: '',
                            password: '',
                            open_id: res.openid,
                            is_authorization: 1
                        }).then((res) => {
                            // 

                            if (res.error == 0) {
                                cacheData = Object.assign(cacheData || {}, {
                                    userId: res.uid
                                })
                                that.Cacher.setData('needBindWx', {
                                    testWx: true, //
                                    needBind: false, //true
                                    haveBind: true, //
                                })
                                selectShop.call(that).then(r => { //
                                    resolve(res);
                                })
                            } else {
                                that.Cacher.setData('needBindWx', {
                                    testWx: true, //
                                    needBind: true, //true
                                    haveBind: false, //
                                })
                                reject(res);
                            }
                        }).catch(res => {

                            if (res.error == -3) { //
                                that.Cacher.setData('needBindWx', {
                                    testWx: true, //
                                    needBind: false, //true
                                    haveBind: true, //
                                })
                                selectShop.call(that).then(r => { //
                                    resolve(res);
                                })
                            } else {
                                that.Cacher.setData('needBindWx', {
                                    testWx: true, //
                                    needBind: true, //true
                                    haveBind: false, //
                                })
                                reject(res)
                            }
                        })
                        uni.getUserInfo({ // 
                            provider: 'weixin',
                            success: function (infoRes) {
                                cacheData = Object.assign(cacheData, infoRes);
                                that.Cacher.setData('login', cacheData);
                            },
                            fail(res) {
                              
                            }
                        });

                    }
                }).catch(res => {
                    that.Cacher.setData('needBindWx', {
                        testWx: true, //
                        needBind: true, //true
                    })
                    reject(res)
                })
            }
        });
    })
}

export function login() { //
    let that = this;

    return new Promise((resolve, reject) => {
        getWxInfo.call(that).then(res => {

            that.Request('login', {
                account: that.userId,
                password: that.password,
                is_authorization: 0
            }).then((res) => {
                // 
                if (res.error == 0) {
                    cacheData = Object.assign(cacheData || {}, {
                        userId: res.uid
                    })
                    that.Cacher.setData('cache-user-login', {
                        userId: that.userId,
                        password: that.password
                    })
                    that.Cacher.setData('login', cacheData);
                    selectShop.call(that).then(r => { //
                        resolve(res);
                    })

                } else {
                    reject(res);
                    that.Toast(res.message)
                }
                that.closePageLoading();
            }).catch(res => {

                if (res.error == -3) { //
                    that.Cacher.setData('login', cacheData)
                    that.Cacher.setData('cache-user-login', {
                        userId: that.userId,
                        password: that.password
                    })
                    selectShop.call(that).then(r => { //
                        resolve(res);
                    })
                } else {
                    if (res.message == '' || res.message == '') { //
                        reject(res);
                    } else {
                        that.Toast(res.message)
                    }
                }
            })

        })

    })

}