let userInfo = null;
let needBindWx = null;

function bindWechat() {
    return new Promise((resolve, reject) => {
        this.Request('bindWechat', {
            encrypted_data: userInfo.encryptedData,
            session_key: userInfo.session_key,
            iv: userInfo.iv,
            user_id: userInfo.userId
        }).then(res => {
            if (res.error == 0) {
                this.Cacher.setData('needBindWx', {
                    testWx: true, //
                    needBind: false, //true
                    haveBindWx: true
                })
                resolve(res)
            } else {
                reject(res)
            }
        }).catch(res => {
            reject(res)
        })
    })

}

export function bindWx(rebind) {
    let that = this;
    return new Promise((resolve, reject) => {
        userInfo = this.Cacher.getData('login');
        needBindWx = this.Cacher.getData('needBindWx');
        if (needBindWx.testWx && needBindWx.needBind && !rebind) {
            this.Request('myInfo').then(res => { //
                if (res.error == 0 && !res.wxapp_openid) {
                    that.Cacher.setData('needBindWx', {
                        testWx: true, //
                        needBind: false, //true
                        haveBindWx: false
                    })
                    that.closePageLoading();
                    that.Dialog.confirm({//
                        title: '',
                        message: '',
                        confirmButtonText: '',
                        confirmButtonOpenType: 'getUserInfo'
                    }).then(() => {
                        that.pageLoading();
                        bindWx.call(that,true);
                    }).catch(res=>{ 
                        that.closePageLoading()
                    });
                }
            })
        } else if (rebind) {//
            let cacheData = that.Cacher.getData('login')
            uni.getUserInfo({ // 
                provider: 'weixin',
                success: function (infoRes) {
                    cacheData = Object.assign(cacheData, infoRes);
                    that.Cacher.setData('login', cacheData);
                    userInfo = cacheData;
                    bindWechat.call(that, userInfo).then(res => {
                        that.closePageLoading();
                        that.Toast('');
                        resolve(res);
                    }).catch(res => {
                        that.closePageLoading();
                        that.Toast('');
                        reject(res);
                    })
                },
                fail(res) {
                    that.closePageLoading()
                    that.Toast('')
                }
            });

        }
    })

}